import React from 'react';
import { useTranslation } from 'react-i18next';

function Home() {
  const { t } = useTranslation();
  return (
    <>
      <h1>{t('pages.home.header')}</h1>
      <p>
        Vtejte na serveru
        {' '}
        <b>var.astro.cz</b>
        , internetovm centru esk promnainy!
      </p>

      <p>
        <b>Sekce promnnch hvzd a exoplanet esk astronomick spolenosti</b>
        {' '}
        ji od roku 1924 organizuje vzkum a pozorovn promnnch hvzd
        v R a SR a sdruuje amatrsk i profesionln
        vzkumnky na tomto zajmavm poli stelrn astrofyziky.
      </p>

      <p>
        Hlavn innost Sekce spov v organizovn spolenho vzkumu
        promnnch hvzd pod 4 vzkumnmi projekty:

      </p>

      <ul>
        <li>
          Projekt
          {' '}
          <b>B.R.N.O.</b>
          {' '}
          - pozorovn a vzkum zkrytovch dvojhvzd
        </li>
        <li>
          Projekt
          {' '}
          <b>MEDZA</b>
          {' '}
          - pozorovn a vzkum fyzickch promnnch hvzd (pevn pulzujcch promnnch hvzd)
        </li>
        <li>
          Projekt
          {' '}
          <b>TRESCA</b>
          {' '}
          - sledovn tranzit exoplanet pes mateskou hvzdu,
          hledn dalch exoplanet u znmch tranziter a hledn novch tranziter.
        </li>
      </ul>

    </>
  );
}

export default Home;
