/* eslint-disable no-promise-executor-return */
function FakeValidator(ms: number) {
  // Simulate uploading the file to the server and getting the response
  const star = {
    name: 'CzeV371 Aur',
    JD: 'heliocentric',
    filter: 'clear',
    coordinates: {
      RA: '05:47:35.553',
      DE: '+31:18:04.48',
    },
  };

  return new Promise((resolve) => setTimeout(resolve, ms)).then(() => star);
}

export default FakeValidator;
